html {
  font-size: 100px;
}

body {
  min-height: 100vh;
  background-color: #282828;
  background-image: radial-gradient(#000 15%, rgba(0, 0, 0, 0) 16%), radial-gradient(#000 15%, rgba(0, 0, 0, 0) 16%), radial-gradient(rgba(255, 255, 255, .1) 15%, rgba(0, 0, 0, 0) 20%), radial-gradient(rgba(255, 255, 255, .1) 15%, rgba(0, 0, 0, 0) 20%);
  background-position: 0 0, 8px 8px, 0 1px, 8px 9px;
  background-repeat: repeat, repeat, repeat, repeat;
  background-size: 16px 16px;
  background-attachment: fixed;
  background-origin: padding-box, padding-box, padding-box, padding-box;
  background-clip: border-box, border-box, border-box, border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: sans-serif;
  display: flex;
}

.arrow {
  width: 250px;
  transition: all .2s;
  display: inline-block;
  overflow: hidden;
  transform: rotate(0deg);
}

h1 {
  color: #fff;
  align-items: center;
  font-size: 60px;
  font-weight: 100;
  display: flex;
}

.units {
  font-size: 15px;
}

